import logo from "./logo.svg";
import "./App.css";
import "./components/CodeForm";
import CodeForm from "./components/CodeForm";
import { useState, useEffect } from "react";

const App = () => {
    const [secret, setSecret] = useState(false);
    const [success, setSuccess] = useState(false);

    useEffect(() => {
        let timeout = null;
        if (secret === true) {
            timeout = setTimeout(() => {
                setSecret(false);
            }, 2000);
        }

        return () => {
            if (timeout) clearTimeout(timeout);
        };
    }, [secret]);

    const showSecret = () => {
        setSecret(true);
    };

    return (
        <div className="App">
            {!success ? (
                <header className="App-header">
                    <div>
                        {secret ? (
                            <div className="secret-text">
                                Sir Hnaljszr tli estken nagyon szeretett
                                ktgetni s volt neki egy kedvenc
                                varrsdoboza...
                            </div>
                        ) : (
                            ""
                        )}
                    </div>
                    <div onClick={showSecret} className="App-logo-link">
                        <img src={logo} className="App-logo" alt="logo" />
                    </div>
                    <p>Krem a titkos jelszt!</p>
                    <CodeForm onSuccess={() => setSuccess(true)} />
                </header>
            ) : (
                <header className="App-header">
                    <div>
                        Gratullok! A megfejts helyes. Had ajnljak cserbe egy
                        nagyszer knyvet: PHP fejleszts felsfokon ;)
                    </div>
                </header>
            )}
        </div>
    );
};

export default App;
